.tabs_border_box {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
}

.tabs_border {
  position: relative;
  -ms-flex: 1 1;
  flex: 1 1;
  text-align: center;
  font-size: .373333rem;
  color: #666;
  border-bottom: 1PX solid #ddd;
  line-height: 1.066667rem;
}

.tabs_border .tabs_border_tag {
  position: relative;
}

.tabs_border_active {
  color: #3190e8;
  font-weight: 700;
}

.tabs_border_active .tabs_border_tag {
  position: relative;
}

.tabs_border_active .tabs_border_tag::after {
  content: "";
  position: absolute;
  bottom: -0.266667rem;
  left: 0;
  right: 0;
  height: .08rem;
  background-color: #2395ff;
}


