.login {
  padding-top: 40PX;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-direction: column;
  flex-direction: column;
}

.login .login_bg {
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background: #fff;
}

.login .login_logo {
  text-align: center;
}

.login .login_logo svg {
  width: 140PX;
  height: 56PX;
}

.login .login_method {
  margin-top: 40PX;
  font-size: 0;
}

.login .login_method a {
  font-size: 14PX;
  padding-bottom: 0.053333rem;
  cursor: pointer;
  color: #000;
}

.login .login_method a:first-child {
  margin-right: 40PX;
}

.login .login_method .login_method_cur {
  color: #2395ff;
  font-weight: 700;
  border-bottom: 2PX solid #2395ff;
}

.login form {
  width: 300PX;
}

.login .login_now {
  display: block;
  width: 100%;
  height: 42PX;
  margin-top: 30PX;
  border-radius: 4PX;
  background: #4cd96f;
  color: #fff;
  cursor: pointer;
  text-align: center;
  font-size: 16PX;
  line-height: 42PX;
  outline: none;
  border: none;
}

.login .login_tips {
  margin-top: 12PX;
  color: #999;
  font-size: 14PX;
  line-height: 20PX;
}

.login .login_tips a {
  color: #2395ff;
}

.login .login_sec {
  position: relative;
  margin-top: 16PX;
  height: 48PX;
  font-size: 14PX;
  background: #fff;
}

.login .login_sec input:focus {
  border-color: #0089dc;
}

.login .login_sec .get_yzm {
  padding: 0;
  border-radius: 6PX;
  color: #2395ff;
  background: transparent;
  text-align: center;
  font-size: 14PX;
  display: inline-block;
  position: absolute;
  top: 50%;
  right: 0.133333rem;
  outline: 0;
  border: none;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

.login .login_sec .get_yzm[disabled] {
  color: #ccc;
}

.login .login_sec input {
  width: 100%;
  height: 100%;
  left: 0;
  padding-left: 10PX;
  border: 1PX solid #ddd;
  border-radius: 4PX;
  color: #333;
  outline: none;
}

.login_wran {
  position: fixed;
  bottom: 2.56rem;
  left: 50%;
  z-index: 9999;
  padding: .266667rem .533333rem;
  border-radius: .533333rem;
  background: rgba(0, 0, 0, 0.6);
  color: #fff;
  font-size: .32rem;
  line-height: 1.2;
  -webkit-transition: opacity .5s;
  -o-transition: opacity .5s;
  transition: opacity .5s;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
  pointer-events: none;
}

.out {
  opacity: 0;
}


