.activityPage_cover {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  overflow: auto;
  z-index: 100;
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
}

.activityPage_cover .activityPage_cover_box {
  padding-top: 1.173333rem;
  height: 100%;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}

.activityPage_cover .activityPage_cover_box .activityPage_close {
  position: fixed;
  left: 0;
  right: 0;
  bottom: .666667rem;
  margin: auto;
  width: 1.2rem;
  height: 1.2rem;
  border: 1PX solid #888;
  border-radius: 50%;
}

.activityPage_cover .activityPage_cover_box .activityPage_close svg {
  fill: #fff;
  width: 100%;
  height: 100%;
  -webkit-transform: scale(0.3);
  -ms-transform: scale(0.3);
  transform: scale(0.3);
}

.activityPage_cover .activityPage_cover_box .activityPage_main {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  border: .8rem solid transparent;
  border-top-width: 1.066667rem;
  border-bottom-width: 2.4rem;
  overflow: auto;
  background-color: #262626;
  color: #fff;
  -webkit-backdrop-filter: saturate(500%) blur(0.666667rem);
  backdrop-filter: saturate(500%) blur(0.666667rem);
}

.activityPage_cover .activityPage_cover_box .activityPage_main .activityPage_main_star {
  -webkit-transform: scale(2);
  -ms-transform: scale(2);
  transform: scale(2);
}

.activityPage_cover .activityPage_cover_box .activityPage_main h2 {
  text-align: center;
  font-size: 1.5em;
  margin: 0;
  font-weight: 400;
}

.activityPage_cover .activityPage_cover_box .activityPage_main .activityPage_main_list {
  margin-bottom: .186667rem;
  font-size: 0;
}

.activityPage_cover .activityPage_cover_box .activityPage_main .activityPage_main_title {
  text-align: center;
  margin: .8rem 0 .533333rem;
  background-image: -webkit-gradient(linear, left top, right top, from(#555));
  background-image: -webkit-linear-gradient(left, #555);
  background-image: -o-linear-gradient(left, #555);
  background-image: linear-gradient(90deg, #555);
  background-position: 50%;
  background-size: 100% 1PX;
  background-repeat: no-repeat;
  font-weight: 400;
}

.activityPage_cover .activityPage_cover_box .activityPage_main .activityPage_main_title span {
  font-size: .32rem;
  padding: .133333rem .266667rem;
  border-radius: .666667rem;
  border: 1PX solid #555;
  background-color: #262626;
}

.shoplist_header_cover-enter {
  opacity: 0;
}

.shoplist_header_cover-enter.shoplist_header_cover-enter-active {
  opacity: 1;
}

.shoplist_header_cover-leave {
  opacity: 1;
}

.shoplist_header_cover-leave.shoplist_header_cover-leave-active {
  opacity: 0;
}


