.ballFather {
  position: absolute;
  width: 0.533333rem;
  height: 0.533333rem;
  border-radius: 50%;
  -webkit-transition: all 0.6s cubic-bezier(0.52, -0.3, 0.93, 0.54);
  -o-transition: all 0.6s cubic-bezier(0.52, -0.3, 0.93, 0.54);
  transition: all 0.6s cubic-bezier(0.52, -0.3, 0.93, 0.54);
}

.ballSon {
  width: 0.533333rem;
  height: 0.533333rem;
  background: #3190e8;
  border-radius: 50%;
  -webkit-transition: all 0.6s linear;
  -o-transition: all 0.6s linear;
  transition: all 0.6s linear;
}

@-webkit-keyframes ani {
  0 {
    -webkit-transform: scale(0);
    transform: scale(0);
  }

  20% {
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
  }

  40% {
    -webkit-transform: scale(0.8);
    transform: scale(0.8);
  }

  80% {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
  }

  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

@keyframes ani {
  0 {
    -webkit-transform: scale(0);
    transform: scale(0);
  }

  20% {
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
  }

  40% {
    -webkit-transform: scale(0.8);
    transform: scale(0.8);
  }

  80% {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
  }

  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

.animation {
  -webkit-animation: ani .4s linear;
  animation: ani .4s linear;
}


